// TYPOGRAPHY
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700;800;900&display=swap');

// BASE
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
}

// HEADER
header {
  height: 35vh;
  background-image: url('https://cdn.zeplin.io/616c287857d983110c1b7bbf/assets/19098EA4-9071-486C-B724-67F2C0851D25.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 20px 20px 0;

  .wrapper {
    max-width: 1024px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    h1 {
      width: 320px;
      font-size: 26px;
      margin-bottom: 10px;
    }

    p {
      width: 320px;
      font-size: 18px;
    }
  }
}

.controls {
  width: 100%;
  height: 70px;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;

  button {
    min-width: 152px;
    height: 40px;
    margin: 0 5px;
    padding: 9px 35px 10px 36px;
    border-radius: 20px;

    &.call {
      background-color: #24e7c8;
      font-weight: bold;
      font-size: 16px;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 17px;
        height: 16px;
        margin: 3px 0 2px 14px;
        object-fit: contain;
      }
    }

    &.details {
      background-color: #000;
      color: #fff;
      border: solid 2px #fff;
    }
  }
}

// SIDE
aside {
  display: none;
}

// MAIN
.container {
  padding: 60px 20px;
  text-align: center;
  background-color: #fafafa;

  .wrapper {
    max-width: 1024px;
    margin: 0 auto;

    p {
      font-size: 18px;
    }

    .intro {
      h1 {
        font-size: 26px;
        margin-bottom: 21px;
      }
      p {
        color: #666666;
      }
    }

    .card {
      margin-top: 40px;

      h2 {
        margin-top: 14px;
        font-size: 24px;
      }
      p {
        margin-top: 17px;
        color: #717171;
      }
    }
  }
}

@media only screen and (min-width: 768px) {
  header {
    background-image: url('https://cdn.zeplin.io/616c287857d983110c1b7bbf/assets/F4511F8C-5A75-48BD-B9F4-807658C41211.png');
    padding-top: 0;

    .wrapper {
      align-items: flex-start;
      justify-content: center;
      text-align: right;

      h1 {
        font-size: 30px;
        margin-bottom: 18px;
        width: 350px;
      }

      p {
        width: 350px;
      }
    }
  }
  .controls {
    button {
      width: 200px;
    }
  }
}

@media only screen and (min-width: 992px) {
  .controls {
    button {
      width: 152px;
    }
  }
  .container {
    .intro {
      h1 {
        font-size: 32px;
        margin-bottom: 11px;
      }
      p {
        font-size: 22px;
      }
    }

    .cards {
      margin-top: 75px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .card {
        width: 30%;
        margin-top: 0;

        h2 {
          font-size: 22px;
          margin-top: 16px;
        }

        p {
          margin-top: 14px;
        }
      }
    }
  }
}

@media only screen and (min-width: 1200px) {
  body {
    display: flex;
  }
  .content {
    width: 70vw;
    header {
      height: 50vh;

      .wrapper {
        max-width: 1280px;
        h1 {
          font-size: 48px;
          width: 550px;
        }
        p {
          font-size: 22px;
          width: 550px;
        }
      }
    }

    .container {
      height: 50vh;

      .wrapper {
        max-width: 800px;

        .intro {
          h1 {
            font-size: 32px;
          }
        }
      }
    }
  }

  aside {
    width: 30vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000;

    h1 {
      width: 80%;
      color: #fff;
      text-align: center;
      font-size: 30px;
      margin: 70px 0;
    }
    form {
      width: 80%;
      padding: 0 20px;
      display: flex;
      flex-direction: column;

      input {
        height: 50px;
        border: none;
        margin-bottom: 30px;
        font-size: 20px;
        padding: 0 20px;

        &:focus {
          outline: none;
        }
      }

      button {
        margin-top: 50px;
        height: 65px;
        border-radius: 32.5px;
        background-color: #24e7c8;
        font-size: 24px;
        font-weight: bold;
        border: none;
      }
    }
  }

  .controls {
    display: none;
  }
}
